.navigation--projects{
	--background-color: rgba(255, 255, 255, 1);
}

.navigation{
	&--projects{

		& .navigation__list{
			display: flex; 
  			flex-direction: row;
  			align-items: center;
		}

		& .navigation__item{
			position: relative;
			margin: 0 .25em;
			white-space: nowrap;
			overflow: visible;
			max-height: 0;

			transition: all .5s ease-out;
			transition-delay: 0s;

			display: flex; 
  			align-items: flex-start;

			&:hover{
				max-height: 120vh;
				transition-delay: .25s;
			}
		}

		& .navigation__label{
			background-color: var(--background-color);
			color: black;
			padding: 1em 0;
			line-height: 2;
			cursor: pointer;
			z-index: 1;

			line-height: 2;
			display: inline-block;
			vertical-align: middle;
			writing-mode: vertical-rl;

			min-height: 0;
			will-change: min-height;
			transition: all .5s ease-out;

			&:hover{
				min-height: calc(100vh + var(--footer-height));

				&:after{
					color: black;
				}
			}
		}

	}
}
